import Movie from "@/components/MovieCard";
import { fetchMoviesByGenre } from "@/lib/fetchData";
import type { GenreType } from "@/types/global";

export default async function Genre({
  params,
}: {
  params: Promise<GenreType>;
}) {
  const { id, name } = await params;
  const movies = await fetchMoviesByGenre(id);

  return (
    <div className="px-10">
      <h2 className="text-2xl font-bold mb-4 pb-2">{name}</h2>
      <div className="grid grid-cols-6">
        {movies.map((movie) => {
          return (
            <Movie key={movie.id} movie={movie} currentGenreId={String(id)} />
          );
        })}
      </div>
    </div>
  );
}
